import React, { Component } from 'react';
import { Nav , Navbar } from 'react-bootstrap';
import Home from './Home';
import SchInfo from './school/Info';
import SchTeacher from './school/Teacher';
import SchCsp from './school/Consumption';
import SchScore from './school/Score';
import SchAttendance from './school/Attendance';
import ClassPortrait from './class/ClassPortrait';
import StuPortrait from './student/Student';
import AppImproveScore from './application/ImproveScore';
import AppPoverty from './application/PovertyHelper';
import App3In7 from './application/3In7';
import AppDetail from './application/Detail';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../css/main.css';

class Main extends Component {
    constructor(props) {
        super(props);
        this.state = {
            whichShow: 'home',
            menuShow: true,
            switchButtonState: false,
        };
        this.menu = [{
            header: "学校层面",
            body: ["基本信息统计", "师资分配情况", "成绩统计分析", "考勤信息统计", "消费信息统计"],
            content: [<SchInfo/>, <SchTeacher/>, <SchScore/>, <SchAttendance/>, <SchCsp/>]
        }, {
            header: "班级层面",
            body: ["班级画像"],
            content: [<ClassPortrait/>]
        }, {
            header: "学生层面",
            body: ["学生画像"],
            content: [<StuPortrait/>]
        }, {
            header: "应用层面",
            body: ["提升学生成绩", "贫困生帮扶", "七选三推荐系统", "全域数据查询"],
            content: [<AppImproveScore/>, <AppPoverty/>, <App3In7/>, <AppDetail/>],
        }];
        this.animation = {
            slideLeft: {animation: 'slideLeft 320ms ease 0s 1 normal forwards running'},
            slideRight: {animation: 'slideRight 320ms ease 0s 1 normal forwards running'},
        };
        this.handleClick = this.handleClick.bind(this);
    }
    handleClick(idx) {
        if (idx !== 'home') {  // 返回首页不需要隐藏菜单栏
            const menuShow = !this.state.menuShow;
            const switchButtonState = !this.state.switchButtonState;
            this.setState({
                menuShow: menuShow,
                switchButtonState: switchButtonState,
            });
        }
        if (typeof idx === 'string') {  // 区分是点击菜单栏还是点击的按钮
            this.setState({whichShow: idx});
        }
    }
    getContent() {
        if (this.state.whichShow === 'home') {  // 渲染首页
            return <Home/>;
        }
        // 如果不是首页则根据索引渲染
        const numbers = this.state.whichShow.split('-');
        const n1 = parseInt(numbers[0]);
        const n2 = parseInt(numbers[1]);
        const temp = this.menu[n1].content[n2];
        return (
            <React.Fragment>
                <Head pretitle={this.menu[n1].header} title={this.menu[n1].body[n2]}/>
                {temp===undefined?<div>索引错误</div>:temp}
            </React.Fragment>
        );
    }
    render() {
        return (
            <React.Fragment>
                <div className="wrapper">
                    <SwitchButton state={this.state.switchButtonState} click={this.handleClick} animation={this.animation}/>
                    <MenuBar menu={this.menu} default={this.state.whichShow} click={this.handleClick} show={this.state.menuShow}/>
                    <div className='main'>
                        <div className='content' style={((this.state.menuShow)&(this.state.whichShow==='home'))?this.animation.slideRight:this.animation.slideLeft}>
                            {this.getContent()}
                        </div>
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

function MenuBar(props) {
    const slideLeftShow = {animation: 'slideLeft-hide 320ms ease 0s 1 normal forwards running'};
    const slideRightHide = {animation: 'slideRight-show 320ms ease 0s 1 normal forwards running'};
    return (
        <Navbar style={props.show?slideRightHide:slideLeftShow}>
            <div className="brand">
                <a href='javascript: void(0);' className='cursor-pointer' onClick={()=>{
                    props.click('home');
                }}>数智教育</a>
            </div>
            <Nav as="ul" className="flex-column menu" onSelect={k => props.click(k)}>
                {
                    props.menu.map((items, index)=>{
                        return <SingleNav key={index} idx={index} header={items.header} body={items.body} />
                    })
                }
            </Nav>
        </Navbar>
    );
}

