function Head(props) {
    if (props.pretitle !== '班级层面' && props.pretitle !== '学生层面') {
        return (
            <div className='header-body'>
                <div className='row'>
                    <div className='col'>
                        <h6 className='header-pretitle'>{props.pretitle}</h6>
                        <h1 className='header-title'>{props.title}</h1>
                    </div>
                </div>
            </div>
        );
    }
    return (
        <React.Fragment>
        </React.Fragment>
    );
}

export default Main;
